.text {
    color: hsl(278, 68%, 11%);
    font-size:11px;
    margin-bottom: 0.5vh;
}
.el {
    margin: 1vh;
}
.text-input {
    color: #333;
    margin: 0 auto;
    padding: 0.7vw;
    border-radius: 7px;
    width: 90%;
    border: 2px solid;
    display: block;
    transition: all 0.3s;
    background-color: white;
}
.text-input:invalid {
    border: red solid 2px;
}